<template>
  <div class="view">
    <van-nav-bar title="签到日历" left-text="返回" @click-left="$router.go(-1)" left-arrow />
    <ul class="list">
      <li class="item" v-for="(item, index) in list" :key="index">
        <span>{{ item.date.substr(0, 10) }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '../../api';

export default {
  data() {
    return {
      list: []
    };
  },
  mounted() {
    this.getCheckinList();
  },
  methods: {
    async getCheckinList() {
      let res = await api.getCheckinList();
      if (!res) return;
      if (!res.success) {
        return this.$toast(res.errMsg);
      }
      this.list = res.data.list;
    }
  }
};
</script>

<style lang="scss" scoped>
.view {
  background: #eee;
}

.list {
  overflow: hidden;
  padding: 12px;
}

.item {
  padding: 12px;
  background: #fff;
  margin-bottom: 10px;
}
</style>



